<template>
  <div class="box">
    <Tabs v-model="tab_index" type="card" @on-click="handleTabClick">
      <TabPane
        v-for="item in tab_list"
        :key="item.id"
        :label="item.name"
        :name="item.name"
      >
        <SendSms v-if="current(item, '')" />
        <SmsSet v-if="current(item, '')" />
        <SmsTemplateList v-if="current(item, '')" />
        <SmsRecharge v-if="current(item, '')" />
        <SmsGroupSendList v-if="current(item, '')" />
        <SmsSystemSendList v-if="current(item, '')" />
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
  import SmsSet from '@/components/market/sms-market/sms-set'
  import SmsRecharge from '@/components/market/sms-market/sms-recharge'
  import SendSms from '@/components/market/sms-market/send-sms'
  import SmsSystemSendList from '@/components/market/sms-market/sms-system-send-list'
  import SmsGroupSendList from '@/components/market/sms-market/sms-group-send-list'
  import SmsTemplateList from '@/components/market/sms-market/sms-template-list'

  export default {
    name: 'SmsMarket',
    components: {
      SmsTemplateList,
      SendSms,
      SmsRecharge,
      SmsSet,
      SmsSystemSendList,
      SmsGroupSendList,
    },
    data: function () {
      return {
        tab_index: '',
        tab_list: [
          { name: '', id: 1 },
          { name: '', id: 2 },
          { name: '', id: 3 },
          { name: '', id: 4 },
          { name: '', id: 5 },
          { name: '', id: 6 },
        ],
      }
    },
    computed: {
      current() {
        return (item, name) => {
          return name === item.name && this.tab_index === name
        }
      },
    },
    methods: {
      handleTabClick(index) {
        this.tab_index = index
      },
    },
  }
</script>
<style lang="less" scoped></style>
