<template>
  <div class="box">
    <Tabs
      v-model="tab_index"
      name="parent"
      type="card"
      @on-click="handleTabClick"
    >
      <TabPane
        v-for="item in tab_list"
        :key="item.id"
        :label="item.name"
        :name="item.name"
        tab="parent"
      >
        <StarOfficer v-if="current(item, '')" />
        <YiYe v-if="current(item, '')" />
        <RulesSet v-if="current(item, '')" />
        <ExperienceCard v-if="current(item, '')" />
        <Withdraw v-if="current(item, '')" />
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
  import StarOfficer from '@/components/market/fission-system/star-officer'
  import YiYe from '@/components/market/fission-system/yi-ye'
  import ExperienceCard from '@/components/market/fission-system/experience-card'
  import RulesSet from '@/components/market/fission-system/rules-set'
  import Withdraw from '@/components/market/fission-system/withdraw'

  export default {
    name: 'FissionSystem',
    components: { Withdraw, RulesSet, ExperienceCard, YiYe, StarOfficer },
    data: function () {
      return {
        tab_index: '',
        tab_list: [
          { name: '', id: 1 },
          { name: '', id: 2 },
          { name: '', id: 3 },
          { name: '', id: 4 },
          { name: '', id: 5 },
        ],
      }
    },
    computed: {
      current() {
        return (item, name) => {
          return name === item.name && this.tab_index === name
        }
      },
    },
    methods: {
      handleTabClick(index) {
        this.tab_index = index
      },
    },
  }
</script>
<style lang="less" scoped></style>
