<template>
  <div class="box">
    <div class="list">
      <vxe-table
        ref="xTable"
        :cell-style="{ textAlign: 'center' }"
        :column-config="{ resizable: true }"
        :data="tableData"
        header-align="center"
        :height="tableHeight"
        :loading="loading"
        :row-config="{ isHover: true }"
        size="small"
      >
        <vxe-column
          v-for="(item, index) in tableColumns"
          :key="index + item.key"
          :field="item.key"
          :fixed="item.fixed"
          :min-width="item.width"
          show-overflow="title"
          :title="item.title"
        />
        <vxe-column field="status" title="" width="120">
          <template #default="{ row }">
            <i-switch v-model="row.status" @on-change="change" />
          </template>
        </vxe-column>
        <vxe-column title="" width="120">
          <template>
            <span class="bt" @click="showModal('', 'CustomerRemind')">
              
            </span>
          </template>
        </vxe-column>
      </vxe-table>
      <div class="page">
        <Page
          :current="searchData.p"
          :page-size="searchData.page"
          show-elevator
          show-sizer
          size="small"
          :total="searchData.count"
          transfer
          @on-change="currentPage"
          @on-page-size-change="pageSizeChange"
        />
      </div>
    </div>
    <Modal
      v-model="modal.show"
      :footer-hide="true"
      :title="modal.title"
      :width="modal.width"
    >
      <CustomerRemind v-if="modalShow('CustomerRemind')" />
    </Modal>
  </div>
</template>

<script>
  import CustomerRemind from '@/components/market/sms-market/sms-set/customer-remind'
  export default {
    name: 'CustomerRemindList',
    components: { CustomerRemind },

    data: function () {
      return {
        loading: false,
        tableHeight: 0,
        tableData: [
          {
            type: '',
            content:
              '~:,,~',
            status: 1,
          },
        ],
        tableColumns: [
          {
            title: '',
            key: 'type',
          },
          {
            title: '',
            key: 'content',
          },
        ],
        modal: {
          show: false,
          title: '',
          type: false,
          width: 800,
        },
        searchData: {
          p: 1,
          page: 10,
          count: 0,
        },
      }
    },

    computed: {
      modalShow() {
        return (name) => {
          return this.modal.type === name && this.modal.show
        }
      },
    },
    activated() {},
    created() {},
    methods: {
      change() {},
      currentPage(current) {
        this.searchData.p = current
        this.getList()
      },
      pageSizeChange(pageSize) {
        this.searchData.page = pageSize
        this.search()
      },
      search() {
        this.searchData.p = 1
        this.getList()
      },
      showModal(title, type, data = {}) {
        console.log(data)
        this.modal.show = true
        this.modal.title = title
        this.modal.type = type
      },
    },
  }
</script>

